@import url("https://fonts.googleapis.com/css?family=Barlow+Condensed");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol, li {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
}

a {
  text-decoration: none;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.talk {
  padding-left: 20px;
  padding-bottom: 10px;
  background: transparent url(../img/icon_talk.png) no-repeat;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 11px;
  color: #757575;
}

.aside-article_preview:hover {
  z-index: -1;
}

.aside-article_preview:hover .js-aside-article_preview {
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.2);
}

.top {
  display: flex;
  border-radius: 5px 5px 0 0;
  background: #f5f5f5 url(../img/top.jpg) repeat-x;
  padding: 10px 32px 10px;
  align-items: center;
}

.link-top {
  display: flex;
  text-decoration: none;
}

.link-top_btn {
  padding: 1px 4px;
  background-color: #85afbd;
  border-radius: 2px;
  text-transform: uppercase;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  color: white;
  font-size: 14px;
}

.link-top_header {
  color: #757575;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 11px;
  line-height: 18px;
  padding-left: 11px;
  padding-top: 2px;
}

.link-top:hover .link-top_btn {
  background-color: #ae0002;
}

.link-top:hover .link-top_header {
  text-decoration: underline;
}

.icon {
  padding-left: 10px;
}

.search {
  position: absolute;
  top: center;
  right: 34%;
  transition: right 1s easy;
}

.arrow {
  background: transparent url(../img/arrow.png) no-repeat;
  height: 10px;
}

.header {
  background: #1a303d url(../img/main_header.png) no-repeat;
  background-size: cover;
  display: flex;
}

.logo {
  padding: 28px 39px 25px 30px;
  height: 129px;
  width: 215px;
}

.navigation {
  margin: 25px 0;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
}

.help-nav {
  display: flex;
}

.help-nav_item {
  padding-right: 11px;
  color: #a6a6a6;
  text-transform: uppercase;
  font-size: 14px;
}

.help-nav_item:hover {
  text-decoration: underline;
}

.main-nav {
  margin: 14px 0 0 -13px;
  display: flex;
}

.main-nav_item {
  padding: 5px 13px;
  color: #f6f6f6;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 18px;
}

.main-nav_item:hover {
  box-shadow: 1px 1px 0 rgba(255, 255, 255, 0.1), inset 1px 1px 1px rgba(0, 0, 1, 0.28);
  background-color: #092f3b;
}

.article-heading {
  display: flex;
}

.article-heading_head {
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  background: #ae0002;
  border-radius: 3px 0 0 3px;
  padding: 5px 32px 0 12px;
  position: relative;
  color: #f6f6f6;
  font-size: 18px;
  text-transform: uppercase;
}

.article-heading_head::after {
  content: '';
  position: absolute;
  right: -37px;
  top: 0;
  border: 16px solid transparent;
  border-left: 21px solid #ae0002;
}

.article-heading_text {
  font-family: Georgia, serif;
  font-style: normal;
  color: #757575;
  background-color: #cacaca;
  flex: 1 1 auto;
  padding: 8px 0 11px 37px;
  border-radius: 0 3px 3px 0;
  letter-spacing: 0px;
  font-size: 12px;
}

.slider {
  margin-top: 5px;
  height: 256px;
  background: #eec38f url(../img/slide.jpg) no-repeat 65% 13%;
  border-radius: 3px;
  position: relative;
}

.slider_text {
  position: absolute;
  top: 42%;
  left: 5%;
  width: 47%;
  background-color: #1e1e1e;
  padding: 20px 20px;
  color: white;
  font-family: Georgia;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.1px;
}

.main_articles {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 15px;
  margin-bottom: 45px;
}

.article-link {
  border-radius: 5px;
  padding: 16px;
  padding-bottom: 7px;
  background-color: white;
  margin-right: 16px;
  position: relative;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}

.article-link:hover {
  transform: scale(1.5, 1.5);
  z-index: 1;
}

.article-link:last-child {
  margin-right: 0;
}

.main-article_img {
  width: 137px;
  height: 117px;
}

.main-article_head {
  margin: 12px 0 7px 2px;
  color: #042b38;
  font-family: Georgia, serif;
  font-style: normal;
  font-size: 12px;
  line-height: 1.5;
  text-decoration: underline;
}

.main-article_text {
  margin: 0 0 10px 2px;
  color: #757575;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 11px;
  line-height: 18px;
  padding-right: 15px;
}

.main-article_talk {
  position: absolute;
  bottom: 1px;
  right: 13px;
}

.big-article {
  display: grid;
  grid-template-columns: 1fr 3fr;
  margin: 26px 0 20px;
}

.big-article_img {
  width: 168px;
  height: 130px;
}

.big-article_info {
  font-family: Georgia, serif;
  color: #757575;
  font-family: Georgia;
  font-weight: 400;
  line-height: 18px;
  margin: 9px 0 13px 15px;
}

.socials {
  margin-top: 19px;
  display: flex;
}

.tags {
  margin-left: 26px;
}

.tags_link {
  text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.3);
  color: #2b5e5e;
  font-style: italic;
  text-decoration: underline;
}

.tags_link:first-child {
  padding-left: 7px;
}

.share {
  display: flex;
  margin-left: 32px;
}

.share_icon {
  margin-left: 11px;
  padding-left: 20px;
}

.facebook {
  background: transparent url(../img/facebook.png) no-repeat;
}

.twitter {
  background: transparent url(../img/twit.png) no-repeat;
  margin-left: 0;
}

@media screen and (max-width: 979px) {
  body {
    margin-top: 0;
  }
  body .top {
    border-radius: 0;
    background-size: cover;
  }
  ul.help {
    justify-content: center;
  }
  header.main_header nav ul.main_nav {
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 7px;
    margin-right: 50px;
  }
  header.main_header nav ul.main_nav li {
    margin: 5px 5px;
  }
  .articles {
    display: block;
  }
  .articles main {
    width: 100%;
  }
  .articles main .slide {
    background-size: cover;
  }
  .articles main .main_articles {
    margin-left: 30px;
    margin-right: 30px;
  }
  .articles main .main_articles article.main_article {
    text-align: center;
  }
  .articles aside {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 0;
  }
  .articles aside h2 {
    grid-column-start: 1;
    grid-column-end: 4;
  }
  .articles aside article.aside_article {
    margin: 15px;
  }
  footer {
    margin: 50px 0;
    display: flex;
    justify-content: center;
  }
}

@media screen and (max-width: 840px) {
  .articles main .main_articles {
    margin-left: 0;
    margin-right: 0;
  }
}

@media screen and (max-width: 758px) {
  .top span {
    margin-left: 100px;
  }
  .top_header header.main_header .main_nav {
    padding: 0;
    margin-left: 10px;
    margin-right: 10px;
  }
  .top p {
    line-height: 1;
  }
  header.main_header {
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .articles main .article_header {
    flex-wrap: wrap;
  }
  .articles main .article_header h2 {
    padding-bottom: 8px;
  }
  .articles main .article_header p {
    padding-left: 30px;
  }
  .articles main .slide p {
    top: 30%;
    width: 60%;
  }
  .articles main .main_articles {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: space-around;
  }
  .articles main .main_articles article.main_article {
    width: 40%;
    font-size: 1.5rem;
    margin: 7.5px;
  }
  .articles main .main_articles article.main_article:first-child {
    margin-left: 7.5px;
  }
  .articles main .main_articles article.main_article:last-child {
    margin-right: 7.5px;
  }
  .articles main .main_articles article.main_article .articles main article.main_article span {
    bottom: 15px;
    height: 15px;
  }
  .articles main .main_articles article.main_article .articles main .big_article {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .articles main .main_articles article.main_article .articles aside span.talk {
    padding-bottom: 20px;
  }
}

@media screen and (max-width: 460px) {
  .articles main .article_header h2 {
    font-size: 1.2rem;
  }
  .articles main div.main_articles article.main_article {
    width: 80%;
  }
  .articles main .big_article {
    display: block;
    text-align: center;
  }
  .articles aside {
    display: block;
    text-align: center;
  }
  .articles aside .aside_article {
    width: 80%;
  }
}

.out {
  background: #c8deeb url(../img/bg_image.jpg) no-repeat;
  background-size: cover;
  font-size: 12px;
  padding-bottom: 100px;
}

.content {
  max-width: 1000px;
  margin: 0 auto;
  padding-top: 34px;
}

.articles {
  background-color: #ededed;
  display: flex;
  padding: 30px 15px 20px 32px;
}

.main {
  width: 76%;
}

.aside {
  width: 24%;
  padding: 0 15px 0 26px;
}

.footer {
  margin: 280px 2px 150px;
}

.aside-heading {
  background-color: #ae0002;
  text-transform: uppercase;
  padding: 4px 12px 8px;
  border-radius: 3px;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 400;
  letter-spacing: 0.15px;
}

.aside-heading_link {
  color: #f6f6f6;
  display: block;
}

.aside-article {
  margin: 5px 0;
}

.aside-article_previw {
  width: 171px;
  height: 186px;
}

.aside-article_heading {
  display: block;
  color: #042b38;
  font-family: Georgia, serif;
  font-style: normal;
  font-size: 12px;
  text-decoration: underline;
  padding-left: 3px;
  letter-spacing: 0;
  line-height: 1.5;
  margin-top: 14px;
}

.aside-article_text {
  margin-top: 6px;
  margin-bottom: 20px;
  color: #757575;
  font-family: Georgia, serif;
  font-style: italic;
  font-size: 11px;
  line-height: 18px;
  padding-left: 3px;
}

.aside-article_talk {
  margin-left: 12px;
}
